@import "./theme.scss";

#Media {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  min-height: 100vh;
  text-align: left;
  width: 100%;

  @media (max-width: $mobile) {
    width: 100%;
    padding: 0 16px;
  }

  .color {
    font-weight: 700;
    color: $dark-shades;
  }
}
